<template>
    <div class="record">
        <nav-bar title="我的翻倍记录"/>
        <div class="income__list-header">
            <div class="income__list-label">金币收益</div>
            <div class="income__list-duration">近7天</div>
        </div>
        <van-list
            v-if="showList"
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="getList"
        >
            <income-list
                class="income__list-list"
                :income-list="list" />
        </van-list>
        <div
            v-else
            class="empty">
            去翻倍一次就有记录啦~
        </div>
    </div>
</template>

<script>
import IncomeList from '@/views/income/IncomeList';
import { getrecordlist } from '@/api/lottery';
import NavBar from '@/components/NavBar';

export default {
    name: 'LotteryRecord',
    components: {
        IncomeList,
        NavBar,
    },
    data() {
        return {
            loading: false,
            list: [],
            finished: false,
            page: 0,
            showList: true,
        };
    },
    methods: {
        getList() {
            this.loading = true;
            this.page += 1;
            getrecordlist({ page: this.page }).then((res) => {
                this.list = this.list.concat(res);
                if (this.list.length === 0) {
                    this.showList = false;
                }
                // 最大条数为20,单次不足20条即为拉取完
                this.finished = res.length < 20;
                // 加载状态结束
                this.loading = false;
                this.$root.hidePlaceholder();
            }).catch(() => {
                this.$router.replace({ name: 'error' });
            });
        },
    },
    mounted() {
        this.$o2o({
            e_c: '大转盘',
            e_a: '曝光',
            e_n: '我的翻倍记录',
        });
    },
};
</script>


